<template>
  <div id="app">
    <audio src="../static/music.mp4" loop class="audio"></audio>
    <router-view/>
  </div>
</template>

<script>
export default {
  name: 'App',
  data () {
    return {
      audio: null
    }
  },
  mounted () {
    document.addEventListener('DOMContentLoaded', function () {
      function audioAutoPlay () {
        this.audio = document.getElementsByClassName('audio')[0]
        this.audio.play()
        document.addEventListener('WeixinJSBridgeReady', function () {
          this.audio.play()
        }, false)
      }
      audioAutoPlay()
    })
  }
}
</script>

<style>
  html {
    height: 100%;
    overflow: hidden;
  }
  body {
    margin: 0;
    height: 100%;
    overflow: hidden;
  }
  #app {
    height: 100%;
    overflow: hidden;
  }
</style>



